// функция, которая настраивает запрос, посылает запрос на сервер, получает ответ и трансформирует ответ в объект
// такие функции называют сервисами и обычно выносят их в отдельную папку и файлы
const postData = async (url, data) => {
    const res = await fetch(url, { 
        method: 'POST',
        headers: {
            'Content-type': 'application/json'
        },
        body: data
    });

    return await res.json();
};

async function getResource(url) {
    const res = await fetch(url);

    if(!res.ok){
        throw new Error(`Could not fetch ${url}, status: ${res.status}`);
    }

    return await res.json();
}

export { postData };
export { getResource };